import React from "react";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import CompareArrowsIcon from "@material-ui/icons/CompareArrows";
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import IndeterminateCheckBoxIcon from "@material-ui/icons/IndeterminateCheckBox";
import { withStyles } from "@material-ui/core/styles";
import states from "../api/states";
import { editExpGroup, deleteExpGroup, } from "../api/api";
import {
  Button,
  ButtonGroup,
  FormGroup,
  Form,
  Label,
  Input,
  Card,
  CardBody,
  CardHeader,
  Col,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  Row,
  Alert,
} from 'reactstrap';

const defaultToolbarSelectStyles = {
  iconButton: {
  },
  iconContainer: {
    marginRight: "24px",
  },
  inverseIcon: {
    transform: "rotate(90deg)",
  },
};

class CustomToolbarSelectExpGroups extends React.Component {
  constructor(props)
  {
    super(props);
    this.state = {
        showEditModal:false,
        saved:false,
        id: '',
        name : '',
        description : '',
        xtoken:'',
        msg: '',
        error: '',
    };
  }
  hideEditModal = () => {
    this.setState({
      ...this.state,
      showEditModal:false,
    });
    if(this.state.saved)
    {
      this.props.rerenderParentCallback();
    }
  }

    handleEditNameInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, name: v,});
    }
    handleEditDescriptionInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, description: v,});
    }
  handleEditSaveChanges = () => {
      editExpGroup(this.state.xtoken, this.state, this.state.id)
      .then((response) => {
        //   console.log('save error', response);
          if( response.status === 200)
          {
            this.setState({
              ...this.state,
              saved:true,
              error: '',
              msg: response.message,
            });
          }
          else
          {
              this.setState({
                  ...this.state,
                  error: response.message,
                  msg: '',
              });
          }
      })
      .catch((error) => {
          this.setState({
              ...this.state,
              error: 'Connection error occured',
          });
          console.log('error', error);
      });
  }
  handleClickEditRow = () => {
    let dataIndex = this.props.selectedRows.data[0]['dataIndex'];
    let selectedRowData = this.props.originalData[dataIndex];
    this.setState({
        showEditModal:true,
        id: selectedRowData.id,
        name : selectedRowData.name,
        description : selectedRowData.description,
        xtoken:this.props.Xtoken,
    });
  }
  handleDeleteSelection = () => {
    let dataIndex = this.props.selectedRows.data[0]['dataIndex'];
    let selectedRowData = this.props.originalData[dataIndex];
    deleteExpGroup(this.props.Xtoken, selectedRowData.id)
    .then((response) => {
      if(response.status === 200 )
      {
        alert(response.message);
        this.props.rerenderParentCallback();
        return;
      }
      alert(response.message);
      return;
    })
    .catch((error) => {
      alert('Connection error occured');
    });
  };

  handleClickDeselectAll = () => {
    this.props.setSelectedRows([]);
  };

  handleClickBlockSelected = () => {
    console.log(`block users with dataIndexes: ${this.props.selectedRows.data.map(row => row.dataIndex)}`);
  };

  render() {
    const { classes } = this.props;

    return (
      <div className={classes.iconContainer}>

        <Tooltip title={"Edit row"}>
          <IconButton className={classes.iconButton} onClick={this.handleClickEditRow}>
            <EditIcon className={classes.icon} />
          </IconButton>
        </Tooltip>
        <Tooltip title={"Deselect row"}>
          <IconButton className={classes.iconButton} onClick={this.handleClickDeselectAll}>
            <IndeterminateCheckBoxIcon className={classes.icon} />
          </IconButton>
        </Tooltip>
        <Tooltip title={"Delete row"}>
          <IconButton className={classes.iconButton} onClick={this.handleDeleteSelection}>
            <DeleteIcon className={classes.icon} />
          </IconButton>
        </Tooltip>
        {/* modals */}
        <Modal
          size="lg"
          isOpen={this.state.showEditModal}
          backdrop={false}>
          <ModalHeader>
            Edit expense group
            {this.state.error.length > 0 && (
              <div>
                <br></br><Alert color="danger">{this.state.error}</Alert>
              </div>
            )}
            {this.state.msg.length > 0 && (
              <div>
                <br></br><Alert color="success">{this.state.msg}</Alert>
              </div>
            )}
          </ModalHeader>
          <ModalBody>
            <Row>
                <Col xl={6} lg={12} md={12}>
                <Card>
                    <CardBody>
                    <Form>
                        <FormGroup>
                        <Label for="exampleEmail">Group name*</Label>
                        <Input onChange={this.handleEditNameInput}
                            type="text" 
                            value={this.state.name}
                            name="name"
                            placeholder=""
                        />
                        </FormGroup>
                    </Form>
                    </CardBody>
                </Card>
                </Col>

                <Col xl={6} lg={12} md={12}>
                <Card>
                    <CardBody>
                    <Form>
                    <FormGroup>
                        <Label for="exampleEmail">Group description*</Label>
                        <Input onChange={this.handleEditDescriptionInput}
                            type="text" 
                            value={this.state.description}
                            name="desc"
                            placeholder=""
                        />
                        </FormGroup>
                    </Form>
                    </CardBody>
                </Card>
                </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button color="primary" onClick={this.handleEditSaveChanges}>
              Save changes
            </Button>{' '}
            <Button color="secondary" onClick={this.hideEditModal}>
              Close
            </Button>
          </ModalFooter>
        </Modal>
      </div>
    );
  }
}

export default withStyles(defaultToolbarSelectStyles, { name: "CustomToolbarSelectExpGroups" })(CustomToolbarSelectExpGroups);